.card {
	width: 100% !important;
	display: inline-block;
	vertical-align: top;
	margin-bottom: 25px;
}

.img-pokemon {
	margin-top: 25px;
	width: 100px;
	height: 100px;
}

.card-cover {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  -webkit-box-shadow: 0 0 8px rgba(0,0,0,.6);
          box-shadow: 0 0 8px rgba(0,0,0,.6);
}

.btn-pokemon {
	background-color: #ff5959;
	color: white;
	padding-right: 10%;
}

.contador {
	margin-top: 160px;
}


