.Modal {
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 100px;
  background:  white;
  border-radius: 20px;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,.4);
          box-shadow: 0 0 10px rgba(0,0,0,.4);
  background: -webkit-gradient(linear, right top, left top, from(#FF530D), to(#E82C0C));
  background: -webkit-linear-gradient(right, #FF530D, #E82C0C);
  background: -o-linear-gradient(right, #FF530D, #E82C0C);
  background: linear-gradient(to left, #FF530D, #E82C0C); 
  color: white;
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
  margin-top: 22px;
  margin-right: 22px;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}


